import Typography from 'typography'
import sutroTheme from 'typography-theme-sutro'


// sutro
const typography = new Typography({
  ...sutroTheme,
  //  18px -> 16px
  baseFontSize: "16px",
  // overrideThemeStyles: ({ rhythm }, options, styles) => ({
  // })
})

module.exports = typography



